import React from 'react';
import { Helmet } from 'react-helmet';

import { css } from '@emotion/core';

import { Footer } from '../components/Footer';
import SiteNav from '../components/header/SiteNav';
import { PostFullContent } from '../components/PostContent';
import { Wrapper } from '../components/Wrapper';
import IndexLayout from '../layouts';
import {
  inner,
  outer,
  SiteArchiveHeader,
  SiteHeader,
  SiteMain,
  SiteNavMain,
} from '../styles/shared';
import { NoImage, PostFull, PostFullHeader, PostFullTitle } from '../templates/post';
import { colors } from '../styles/colors';

const PageTemplate = css`
  .site-main {
    margin-top: 64px;
    padding-bottom: 4vw;
    background: #fff;
  }

  //@media (prefers-color-scheme: dark) {
    .site-main {
      /* background: var(--darkmode); */
      background: ${colors.darkmode};
    }
  //}
`;

import config from '../website-config';
import { graphql, Link } from 'gatsby';

const Privacy: React.FC = props => {

  let cardImage = `${config.siteUrl}${props.data.header.childImageSharp.fixed.src}`
  let { width, height } = props.data.header.childImageSharp.fixed;

  return (
  <IndexLayout>
    <Helmet>
      <title>{`Privacy - ${config.title}`}</title>
        <meta name="description" content={'Privacy'} />
        <meta property="og:site_name" content={config.title} />
        <meta property="og:type" content="website" />
        <meta property="og:title" content={`Privacy - ${config.title}`} />
        <meta property="og:description" content={'Privacy'} />
        <meta property="og:url" content={config.siteUrl + 'privacy'} />
        {config.facebook && <meta property="article:publisher" content={config.facebook} />}
        <meta name="twitter:card" content="summary_large_image" />
        <meta name="twitter:title" content={`Privacy - ${config.title}`} />
        <meta name="twitter:url" content={config.siteUrl + 'privacy'} />
        <meta property="twitter:description" content={'Privacy'} />
        {config.twitter && (
          <meta
            name="twitter:site"
            content={`@${config.twitter.split('https://twitter.com/')[1]}`}
          />
        )}
        <meta name="og:image" content={cardImage} />
        <meta name="twitter:image" content={cardImage} />
        <meta property="og:image:width" content={width.toString()} />
        <meta property="og:image:height" content={height.toString()} />
    </Helmet>
    <Wrapper css={PageTemplate}>
      <header className="site-archive-header no-image" css={[SiteHeader, SiteArchiveHeader]}>
        <div css={[outer, SiteNavMain]}>
          <div css={inner}>
            <SiteNav isHome={false} isPost={true} post={{title: 'Privacy'}} />
          </div>
        </div>
      </header>
      <main id="site-main" className="site-main" css={[SiteMain, outer]}>
        <div css={inner}>
          <article className="post page" css={[PostFull, NoImage]}>
            <PostFullHeader className="post-full-header">
              <PostFullTitle className="post-full-title">Privacy</PostFullTitle>
            </PostFullHeader>

            <PostFullContent className="post-full-content">
  <div className="post-content">
  <h5>
  The Empty Robot Privacy Policy
  </h5>
  <br/>
  <p>
  This Privacy Policy describes how your personal information is collected, used, and shared when you visit or make a purchase from https://theemptyrobot.com (the “Site”).
  </p>


  <h6>
  PERSONAL INFORMATION WE COLLECT
  </h6>
  <br/>
  <p>
  When you visit the Site, we automatically collect certain information about your device, including information about your web browser, IP address, time zone, and some of the cookies that are installed on your device.
  Additionally, as you browse the Site, we collect information about the individual web pages or products that you view, what websites or search terms referred you to the Site, and information about how you interact with the Site.
  We refer to this automatically-collected information as “Device Information.”
  </p>
  <p>
  We collect Device Information using the following technologies:
  </p>
  <div>
  <ul>

  <li>
   “Cookies” are data files that are placed on your device or computer and often include an anonymous unique identifier. For more information about cookies, and how to disable cookies, visit http://www.allaboutcookies.org.
  </li>
  <li>
  “Log files” track actions occurring on the Site, and collect data including your IP address, browser type, Internet service provider, referring/exit pages, and date/time stamps.
  </li>
  <li>
  “Web beacons,” “tags,” and “pixels” are electronic files used to record information about how you browse the Site.
  </li>

  </ul>

Additionally when you make a purchase or attempt to make a purchase through the Site, we collect certain information from you, including your name, billing address, shipping address, payment information (including credit card numbers), email address, and phone number.
  We refer to this information as “Order Information.”

  </div>
  <p>

When we talk about “Personal Information” in this Privacy Policy, we are talking both about Device Information and Order Information.

  </p>

  <h6>
HOW DO WE USE YOUR PERSONAL INFORMATION?
  </h6>
  <br/>

  <p>
We use the Order Information that we collect generally to fulfill any orders placed through the Site (including processing your payment information, arranging for shipping, and providing you with invoices and/or order confirmations).

  </p>
  <p>
  Additionally, we use this Order Information to:
Communicate with you;
Screen our orders for potential risk or fraud; and
When in line with the preferences you have shared with us, provide you with information or advertising relating to our products or services.
  </p>
  <p>

We use the Device Information that we collect to help us screen for potential risk and fraud (in particular, your IP address), and more generally to improve and optimize our Site (for example, by generating analytics about how our customers browse and interact with the Site, and to assess the success of our marketing and advertising campaigns).
  </p>

  <h6>
SHARING YOUR PERSONAL INFORMATION
  </h6>
  <br/>

  <p>
We share your Personal Information with third parties to help us use your Personal Information, as described above.
  For example, we use Google Analytics to help us understand how our customers use the Site--you can read more about how Google uses your Personal Information here:  https://www.google.com/intl/en/policies/privacy/.
You can also opt-out of Google Analytics here:  https://tools.google.com/dlpage/gaoptout.
  </p>
  <p>

Finally, we may also share your Personal Information to comply with applicable laws and regulations, to respond to a subpoena, search warrant or other lawful request for information we receive, or to otherwise protect our rights.
  </p>


  <h6>
BEHAVIOURAL ADVERTISING
  </h6>

  <br/>
  <p>
As described above, we use your Personal Information to provide you with targeted advertisements or marketing communications we believe may be of interest to you.
  For more information about how targeted advertising works, you can visit the Network Advertising Initiative’s (“NAI”) educational page at http://www.networkadvertising.org/understanding-online-advertising/how-does-it-work.
  </p>
  <div>

You can opt out of targeted advertising by:
<ul>
  <li>
    Facebook - https://www.facebook.com/settings/?tab=ads
  </li>
  <li>
    Google - https://www.google.com/settings/ads/anonymous
  </li>
  <li>
    Bing - https://advertise.bingads.microsoft.com/en-us/resources/policies/personalized-ads
  </li>
  </ul>
  </div>
  <p>

Additionally, you can opt out of some of these services by visiting the Digital Advertising Alliance’s opt-out portal at:  http://optout.aboutads.info/.
  </p>

  <h6>
DO NOT TRACK
  </h6>
  <br/>
  <p>
Please note that we do not alter our Site’s data collection and use practices when we see a Do Not Track signal from your browser.

  </p>

  <h6>
DATA RETENTION
  </h6>
  <br/>
  <p>
When you place an order through the Site, we will maintain your Order Information for our records unless and until you ask us to delete this information.
  </p>
  <h6>
MINORS
  </h6>
  <br/>
  <p>
  The Site is not intended for individuals under the age of 18.
  </p>
  <h6>
  CHANGES
  </h6>
  <br/>
  <p>
  We may update this privacy policy from time to time in order to reflect, for example, changes to our practices or for other operational, legal or regulatory reasons.
  </p>

  <h6>
  CONTACT US
  </h6>
  <br/>
  <p>
  For more information about our privacy practices, if you have questions, or if you would like to make a complaint, please contact us by e-mail at support@theemptyrobot.com.
  </p>
  </div>



            </PostFullContent>
          </article>
        </div>
      </main>
      <Footer />
    </Wrapper>
  </IndexLayout>
); };

export default Privacy;

export const pageQuery = graphql`
  query {
    header: file(relativePath: { eq: "img/emptyrobot-background.png" }) {
      childImageSharp {
        # Specify the image processing specifications right in the query.
        # Makes it trivial to update as your page's design changes.
        fixed(width: 1200, quality: 100) {
          ...GatsbyImageSharpFixed
        }
      }
    }
  }
`;
